.svg-scene {
  height: 100vh;

  svg {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    animation: sky 2s ease forwards;
    background-color: #e2fdff;

    @keyframes sky {
      from {
        background-color: #fff;
      } to {
        background-color: #e2fdff;
      }
    }

    .grass {
      transform: translateY(calc(100vh - 120px));

      .blade {
        animation: grow ease;
        transform-origin: 50% 100%;

        @keyframes grow {
          from {
            transform: scaleY(0);
          }
          to {
            transform: scaleY(1);
          }
        }
      }
    }

    .sun {
      fill: yellow;
      transform-origin: center;
      animation: moveIn 5s ease-in-out 1 forwards;

      @keyframes moveIn {
        0% {
          transform: translate(0, 0);
        }
        100% {
            transform: translate(100vw, -180px);
        }
      }

      .sun-shine {
        stroke: yellow;
        stroke-width: 2px;
      }
    }
  }
}
